.container {
  background: #fff;
  border-radius: 4px;
  padding: 20px;
}
.container .buttons {
  position: relative;
  margin-bottom: 25px;
  display: flex;
}
.container .buttons :global(.anticon) {
  margin-right: 0;
}
.container .buttons > button {
  margin-right: 10px;
}
.container .buttons > button span {
  color: #fff;
}
.container .buttons > button i {
  color: #fff;
}
.container .buttons span {
  color: #1890ff;
}
.table {
  border-radius: 4px;
}
.table :global .ant-table-thead tr th {
  background-color: #F4F7F9;
}
.table :global .ant-table-thead .ant-checkbox:not(.ant-checkbox-disabled):not(.ant-checkbox-checked):hover {
  position: relative;
}
.table :global .ant-table-thead .ant-checkbox:not(.ant-checkbox-disabled):not(.ant-checkbox-checked):hover::before {
  content: '';
  position: absolute;
  z-index: 1;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
  left: 3px;
  top: 20px;
  border-width: 0 5px 5px;
  border-bottom-color: rgba(0, 0, 0, 0.75);
}
.table :global .ant-table-thead .ant-checkbox:not(.ant-checkbox-disabled):not(.ant-checkbox-checked):hover::after {
  content: '\8BE5\5168\9009\5E94\7528\4E8E\6240\6709\9875';
  position: absolute;
  top: 25px;
  left: -15px;
  z-index: 1;
  max-width: 250px;
  padding: 6px 8px;
  color: #fff;
  text-align: left;
  text-decoration: none;
  background-color: rgba(0, 0, 0, 0.75);
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  min-height: 32px;
  line-height: 1.5;
  white-space: nowrap;
}
.table :global .ant-table-pagination.ant-pagination {
  margin: 16px 10px;
}
.table :global .ant-table-body::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
.table :global .ant-table-body::-webkit-scrollbar-track-piece {
  background-color: #f1f1f1;
}
.table :global .ant-table-body::-webkit-scrollbar-thumb {
  width: 10px;
  border-radius: 5px;
  background-color: #a8a8a8;
}
